@use "../base/variables" as v;
@use '../base/mixins' as m;

.header {
    background-color: v.$verde;

    @include m.tablet {
        &.fijo {
            left: 0;
            top: 0;
            position: fixed; //para que la barra se sobreponga y siempre se vea el header
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }
    }
    h1 {
        @include m.tablet {
            margin: 0;

        }

        color: v.$blanco;
        text-align: center;
    }

    .contenido-header {
        padding: 2rem;

        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }

}

.navegacion-principal {

    @include m.tablet {
        display: flex;
        gap: 2rem;
    }


    a {
        @include m.tablet {
            margin: 0;
        }

        display: block;
        text-align: center;
        color: v.$blanco;
        font-size: 2.4rem;
        margin-bottom: 1rem;

        &:hover {
            color: v.$amarillo;
        }
    }
}